import React, { Component } from "react";

export default class About extends Component {
  render() {
    const { about, aboutStrings } = this.props;
    return (
      <section id="about">
        <div className="row">
          <div className="two columns">
            <img className="profile-pic" src="images/profile.jpeg" alt="" />
          </div>
          <div className="ten columns main-col">
            <h2>{aboutStrings.aboutMe}</h2>
            <p className="aboutMe">{about.aboutMe}</p>
            <p className="aboutMeEmphasized">{about.aboutMeEmphasized}</p>
            <div className="row">
              {/* <div className="columns contact-details">
                <h2>{aboutStrings.contactDetails}</h2>
                <p className="address">{about.address}</p>
              </div> */}
              {/* <div className="columns download">
                <p>
                  <a
                    href={`/downloads/${about.fileName}`}
                    target="_blank"
                    rel="noopener noreferrer"
                    className="button"
                  >
                    <i className="fa fa-download" />
                    {aboutStrings.download}
                  </a>
                </p>
              </div> */}
            </div>
          </div>
        </div>
      </section>
    );
  }
}
